import Vue from 'vue';
//
import VueRouter from 'vue-router'
Vue.use(VueRouter);
//1.
import Index from '@/views/index'
import Home from '@/views/home'
import Login from '@/views/login'
import NotFound from '@/views/404'
import UpdatePassword from '@/views/update-password'
import pay from '@/views/pay'
import register from '@/views/register'
import center from '@/views/center'
import adminexam from '@/views/modules/shijuanliebiao/exam'
    import cuotiben from '@/views/modules/cuotiben/list'
    import kaoshijilu from '@/views/modules/kaoshijilu/list'
    import xuesheng from '@/views/modules/xuesheng/list'
    import shijuanliebiao from '@/views/modules/shijuanliebiao/list'
    import yinanjieda from '@/views/modules/yinanjieda/list'
    import jiaoshi from '@/views/modules/jiaoshi/list'
    import exampaper from '@/views/modules/exampaper/list'
    import shitiguanli from '@/views/modules/shitiguanli/list'
    import wentifabu from '@/views/modules/wentifabu/list'


//2.   
const routes = [{
    path: '/index',
    name: '',
    component: Index,
    children: [{
      // main
      path: '/',
      name: '',
      component: Home,
      meta: {icon:'', title:'center'}
    }, {
      path: '/updatePassword',
      name: '',
      component: UpdatePassword,
      meta: {icon:'', title:'updatePassword'}
    }, {
      path: '/pay',
      name: '',
      component: pay,
      meta: {icon:'', title:'pay'}
    }, {
      path: '/center',
      name: '',
      component: center,
      meta: {icon:'', title:'center'}
    }
          ,{
	path: '/cuotiben',
        name: '',
        component: cuotiben
      }
          ,{
	path: '/kaoshijilu',
        name: '',
        component: kaoshijilu
      }
          ,{
	path: '/xuesheng',
        name: '',
        component: xuesheng
      }
          ,{
	path: '/shijuanliebiao',
        name: '',
        component: shijuanliebiao
      }
          ,{
	path: '/yinanjieda',
        name: '',
        component: yinanjieda
      }
          ,{
	path: '/jiaoshi',
        name: '',
        component: jiaoshi
      }
          ,{
	path: '/exampaper',
        name: '',
        component: exampaper
      }
          ,{
	path: '/shitiguanli',
        name: '',
        component: shitiguanli
      }
          ,{
	path: '/wentifabu',
        name: '',
        component: wentifabu
      }
        ]
  },
  {
    path: '/adminexam',
    name: 'adminexam',
    component: adminexam,
    meta: {icon:'', title:'adminexam'}
  },
  {
    path: '/login',
    name: 'login',
    component: Login,
    meta: {icon:'', title:'login'}
  },
  {
    path: '/register',
    name: 'register',
    component: register,
    meta: {icon:'', title:'register'}
  },
  {
    path: '/',
    name: '',
    redirect: '/index'
  }, /**/
  {
    path: '*',
    component: NotFound
  }
]
//3.VueRouter  
const router = new VueRouter({
  mode: 'hash',
  /*hashhistory*/
  routes //  routes: routes
})

export default router;
