<template>
  <div>
    <el-form
      class="detail-form-content"
      ref="ruleForm"
      :model="ruleForm"
      label-width="80px"
    >  
     <el-row>
                              <el-col :span="12">
        <el-form-item  v-if="flag=='xuesheng'"  label="" prop="xuehao">
          <el-input v-model="ruleForm.xuehao" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="flag=='xuesheng'"  label="" prop="mima">
          <el-input v-model="ruleForm.mima" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="flag=='xuesheng'"  label="" prop="xingming">
          <el-input v-model="ruleForm.xingming" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item v-if="flag=='xuesheng'"  label="" prop="xingbie">
          <el-select v-model="ruleForm.xingbie" placeholder="">
            <el-option
                v-for="(item,index) in xueshengxingbieOptions"
                v-bind:key="index"
                :label="item"
                :value="item">
            </el-option>
          </el-select>
        </el-form-item>
      </el-col>
                        <el-col :span="24">  
        <el-form-item v-if="flag=='xuesheng'" label="" prop="touxiang">
          <file-upload
          tip=""
          action="file/upload"
          :limit="3"
          :multiple="true"
          :fileUrls="ruleForm.touxiang?ruleForm.touxiang:''"
          @change="xueshengtouxiangUploadChange"
          ></file-upload>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="flag=='xuesheng'"  label="" prop="yuanxi">
          <el-input v-model="ruleForm.yuanxi" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="flag=='xuesheng'"  label="" prop="banji">
          <el-input v-model="ruleForm.banji" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="flag=='xuesheng'"  label="" prop="shouji">
          <el-input v-model="ruleForm.shouji" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                                                <el-col :span="12">
        <el-form-item  v-if="flag=='jiaoshi'"  label="" prop="gonghao">
          <el-input v-model="ruleForm.gonghao" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="flag=='jiaoshi'"  label="" prop="mima">
          <el-input v-model="ruleForm.mima" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="flag=='jiaoshi'"  label="" prop="xingming">
          <el-input v-model="ruleForm.xingming" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item v-if="flag=='jiaoshi'"  label="" prop="xingbie">
          <el-select v-model="ruleForm.xingbie" placeholder="">
            <el-option
                v-for="(item,index) in jiaoshixingbieOptions"
                v-bind:key="index"
                :label="item"
                :value="item">
            </el-option>
          </el-select>
        </el-form-item>
      </el-col>
                        <el-col :span="24">  
        <el-form-item v-if="flag=='jiaoshi'" label="" prop="touxiang">
          <file-upload
          tip=""
          action="file/upload"
          :limit="3"
          :multiple="true"
          :fileUrls="ruleForm.touxiang?ruleForm.touxiang:''"
          @change="jiaoshitouxiangUploadChange"
          ></file-upload>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="flag=='jiaoshi'"  label="" prop="xueyuan">
          <el-input v-model="ruleForm.xueyuan" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="flag=='jiaoshi'"  label="" prop="banji">
          <el-input v-model="ruleForm.banji" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="flag=='jiaoshi'"  label="" prop="dianhua">
          <el-input v-model="ruleForm.dianhua" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                                                                                          <el-form-item v-if="flag=='users'" label="" prop="username">
        <el-input v-model="ruleForm.username" 
        placeholder=""></el-input>
      </el-form-item>
      <el-col :span="24">
      <el-form-item>
        <el-button type="primary" @click="onUpdateHandler"> </el-button>
      </el-form-item>
      </el-col>
      </el-row>
    </el-form>
  </div>
</template>
<script>
// url
import { isNumber,isIntNumer,isEmail,isMobile,isURL,checkIdCard } from "@/utils/validate";

export default {
  data() {
    return {
      ruleForm: {},
      flag: '',
      usersFlag: false,
                                                                  xueshengxingbieOptions: [],
                                                                                                                                    jiaoshixingbieOptions: [],
                                                                                                                                        };
  },
  mounted() {
    var table = this.$storage.get("sessionTable");
    this.flag = table;
    this.$http({
      url: `${this.$storage.get("sessionTable")}/session`,
      method: "get"
    }).then(({ data }) => {
      if (data && data.code === 0) {
        this.ruleForm = data.data;
      } else {
        this.$message.error(data.msg);
      }
    });
                                            this.xueshengxingbieOptions = ",".split(',')
                                                                                        this.jiaoshixingbieOptions = ",".split(',')
                                                                                          },
  methods: {
                                                                                                                                                                                                                                                                xueshengtouxiangUploadChange(fileUrls) {
        this.ruleForm.touxiang = fileUrls;
    },
                                                                                        jiaoshitouxiangUploadChange(fileUrls) {
        this.ruleForm.touxiang = fileUrls;
    },
                                                                                    onUpdateHandler() {
                              if((!this.ruleForm.xuehao)&& 'xuesheng'==this.flag){
        this.$message.error('');
        return
      }
                                                            if((!this.ruleForm.mima)&& 'xuesheng'==this.flag){
        this.$message.error('');
        return
      }
                                                                                                                                                                                                                                                                                                                                                                                    if((!this.ruleForm.gonghao)&& 'jiaoshi'==this.flag){
        this.$message.error('');
        return
      }
                                                            if((!this.ruleForm.mima)&& 'jiaoshi'==this.flag){
        this.$message.error('');
        return
      }
                                                                                                                                                                                                                                                                                                                                                                                                                              this.$http({
        url: `${this.$storage.get("sessionTable")}/update`,
        method: "post",
        data: this.ruleForm
      }).then(({ data }) => {
        if (data && data.code === 0) {
          this.$message({
            message: "",
            type: "success",
            duration: 1500,
            onClose: () => {
            }
          });
        } else {
          this.$message.error(data.msg);
        }
      });
    }
  }
};
</script>
